
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
show: false,
menus: {
menu1: '退出登录'
},
PengyuanUrl:'',
pengyuan_reporStatus:''
}
},
created(){
let pengyuan_reporStatus = this.$route.query.pengyuan_reporStatus;
this.pengyuan_reporStatus = pengyuan_reporStatus;
console.log(pengyuan_reporStatus)
},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton,
Alert,
Toast,
Divider
},
methods:{
getPengyuan(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.buildRequest,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
}
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.PengyuanUrl = res.data.retData.url;
window.location = this.PengyuanUrl;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
openPyReport(){
this.$router.push({
path:'/py_report',
});
},
goRenhang(){
if(this.checkIOS()){
window.location = 'https://ipcrs.pbccrc.org.cn/';
}else {
this.$router.push({
path:'/renhang_credit_msg',
query: {
pengyuan_reporStatus: this.pengyuan_reporStatus
}
});
}

}

}
}



